@import url(https://fonts.googleapis.com/css?family=Open+Sans|Open+Sans+Condensed:300,700);
.footer__nav{
    max-width: 1170;
    margin-left: auto;
    margin-right: auto;
}

.footer__col{
    margin-right: 2.5%;
    width: 14.5%;
    line-height: 25px;
}

.footer__col--intro{
    clear: none;
    border-bottom: 0;
    margin-bottom: 0;
    min-width: auto;
    padding-bottom: 60px;
}

.footer__img{
    width: 150px;
    color: white;
}

.footer__title{
    padding-bottom: 8px;
}

.footer__link{
    color: #9b9b9b;
    display: block;
    font-size: 12px;
    transition: color .75s ease;
}

.footer__link:hover{
    color: #fff;
}
h1, h2, h3, h4, h5 {
	 margin: 0;
	 font-weight: 400;
}
 .card {
	 display: inline-block;
	 width: 300px;
	 padding: 1em;
	 border-radius: 15px;
	 margin: 30px;
	 background: #ddd;
	 text-align: left;
	 box-shadow: 0px 5px 20px -10px #111;
	 position: relative;
	 transition: 0.4s;
}
 .card__caption {
	 background-color: rgba(255, 255, 255, 0.65);
	 padding: 1em;
	 position: relative;
	 border-radius: 0 0 3px 3px;
}
 .card__image-container {
	 background-color: rgba(0, 0, 0, 0.7);
	 max-height: 180px;
	 overflow: hidden;
	 border-radius: 3px 3px 0 0;
}
 .card__type {
	 position: absolute;
	 top: 0;
	 right: 1em;
	 transform: translateY(-50%);
	 color: #fff;
	 text-transform: uppercase;
	 font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
	 letter-spacing: 0.1em;
	 padding: 0.25em;
	 line-height: 1;
	 border-radius: 2px;
	 background: #bbb;
}
 .card__label {
	 font-size: 10px;
	 text-transform: uppercase;
	 font-weight: 400;
	 display: block;
	 margin-bottom: 3px;
}
 .card__name {
	 font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
	 text-align: center;
	 font-size: 1.5em;
	 font-weight: 700;
	 letter-spacing: 0.02em;
}
 .card__stats {
	 margin: 1em 0;
	 width: 100%;
}
 .card__stats th {
	 font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
	 text-align: right;
	 font-weight: 300;
}
 .card__stats th, .card__stats td {
	 width: 50%;
	 padding: 0.25em 0.5em 0;
}
 .card__abilities {
	 display: flex;
	 justify-content: space-between;
}
 .card__ability {
	 margin-top: 1em;
	 flex: 1 0;
	 font-size: 20px;
}
 .card--normal {
	 background: linear-gradient(110deg, #fdbb2d 0%, #3a1c71 100%);
	 box-shadow: 0px 5px 20px -10px #3a1c71;
}
 .card--normal .card__type {
	 background-color: #c08a53;
}
 .card--water {
	 background: linear-gradient(120deg, #1cb5e0 0%, #000851 100%);
	 box-shadow: 0px 5px 20px -10px #000851;
}
 .card--water .card__type {
	 background-color: #1cb5e0;
}
 .card--electric {
	 background: linear-gradient(90deg, rgba(255, 222, 0, 1) 34%, rgba(232, 255, 153, 1) 83%);
}
 .card--electric .card__type {
	 background-color: #000;
}
 .card--fire {
	 background: linear-gradient(0deg, rgba(199, 24, 0, 1) 10%, rgba(252, 194, 69, 1) 100%);
}
 .card--fire .card__type {
	 background-color: rgba(199, 24, 0, 1);
}
 .card--psychic {
	 background: linear-gradient(140deg, rgba(255, 167, 249, 1) 0%, rgba(255, 44, 195, 1) 39%, rgba(255, 227, 167, 1) 100%);
}
 .card--psychic .card__type {
	 background: #ff2cc3;
}
 .card--dark {
	 background: linear-gradient(20deg, rgba(25, 25, 25, 1) 0%, rgba(16, 11, 50, 1) 33%, rgba(92, 2, 73, 1) 100%);
}
 .card--dark .card__type {
	 background: #5c0249;
}
 .card--grass {
	 background: linear-gradient(140deg, rgba(196, 218, 61, 1) 0%, rgba(110, 127, 14, 1) 69%, rgba(39, 80, 9, 1) 100%);
}
 .card--grass .card__type {
	 background: #6e7f0e;
}
 .card--ice {
	 background: linear-gradient(230deg, rgba(202, 234, 246, 1) 0%, rgba(160, 234, 241, 1) 46%, rgba(111, 184, 235, 1) 100%);
}
 .card--ice .card__type {
	 background: #6fb8eb;
}
 .card--fairy {
	 background: linear-gradient(45deg, rgba(255, 230, 240, 1) 0%, rgba(255, 197, 224, 1) 34%, rgba(255, 166, 185, 1) 71%, rgba(255, 138, 149, 1) 100%);
}
 .card--fairy .card__type {
	 background: #ff8a95;
}


 .card:hover {
	 z-index: 5;
	 box-shadow: 0px 13px 30px -15px #000;
	 transform: translateY(-10px);
}
 

 /* Style all font awesome icons */
 .fa {
    padding: 30px;
    font-size: 30px;
    width: 80px;
    text-align: center;
    text-decoration: none;
    margin: 20px;

    background-image: linear-gradient(to right, #028b67, #067f8f); 
    color: white;
  }
  
  /* Add a hover effect if you want */
  .fa:hover {
    opacity: 0.7;
    color: white;
  }
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #2c2f33; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #23272a; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

